<!-- 메인 페이지 마크업 -->
<template lang="html">
    <article>
      <h2 class="hidden">메인 콘텐츠 영역</h2>
      <ul class="search">
        <li>
          <h3 class="hidden">소개</h3>
          <router-link to="/sub1">
            <div>
              <img src="../assets/main-content-1.png" alt="">
            </div>
            <p>책크방은 공공데이터 포털의 API를 이용한 중고서점 검색 애플리케이션입니다. 요즘 현대인들은 1년에 책 한 권을 읽는 사람도 드물다고 합니다. 생각해보면 스마트폰에 익숙해지기 전에는 그래도 책을 꽤 읽었습니다. 길거리를 지나가다 보이는 낡은 서점에 들어가 기분좋게 퀴퀴한 책 냄새를 맡으며 서서 책을 읽곤 했습니다. 또 새 책을 샀을 때는 어떤가요? 이상하게 새 책 냄새를 맡으면서 깔깔 웃곤 하진 않았나요</p>
          </router-link>
        </li>
        <li>
          <h3 class="hidden">책방검색</h3>
          <router-link to="/sub2">
            <p>하지만 오랜만에 책을 읽으려고 해도 근처 교보문고를 가야하나.. 어디서 책을 사야하나 잘 모르시겠죠? 책크방에서는 주변에 있는 중고서점을 "책크"하실 수 있도록 도와 드립니다! 책크방으로 내 근처의 서점은 어디인가 확인해보세요! 서점의 위치는 1년마다 갱신됩니다.</p>
            <div>
              <img src="../assets/main-content-2.png" alt="">
            </div>
          </router-link>
        </li>
      </ul>
      <div class="book">
        <h3>추천도서</h3>
        <swiper class="swiper" :options="swiperOption">
            <swiper-slide v-for="(item,i) in book" :key="i">
              <router-link to="/sub2">
                <img :src="require('../assets/list-'+(i+1)+'.png')">
                <dl>
                  <dt>{{item.dt}}</dt>
                  <dd><span>{{item.span}}</span>{{item.dd}}</dd>
                </dl>
              </router-link>
            </swiper-slide>
          <div class="swiper-button-prev" slot="button-prev"></div>
          <div class="swiper-button-next" slot="button-next"></div>  
        </swiper>
      </div>
    </article>
  </template>
  
  <script>
  import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
  import 'swiper/css/swiper.css';
  import bookJson from '../assets/book.json';

  export default {
    components: {
        Swiper,
        SwiperSlide
    },
   data() {
      return {
        swiperOption: {
          slidesPerView: 1,
          spaceBetween: 30,
          loop: true,
          pagination: {
            clickable: true
          },
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
          },
          breakpoints: {
            1350:{
              slidesPerView: 3
            },
            900:{
              slidesPerView: 2
            }
          }
        },
        book: bookJson,
      }
    }
  }
  </script>
  
  <style>
    article{
      padding: 100px 0 50px 0;
      margin: 0 auto;
    }
    article .search{
      display: flex;
      justify-content: space-between;
      max-width: 1200px;
      margin: 0 auto;
    }
    article .search li{
      width: 45%;
    }
    article .search div{
      overflow: hidden;
      border-radius: 15px;
    }
    article .search li img{
      width: 100%;
      object-fit: contain;
      transform: scale(1);
      transition: all .5s;
    }
    article .search li:hover img{
      transform: scale(1.1);
    }
    article .search p{
      font-size: 1em;
      font-weight: 700;
      text-align: justify;
      line-height: 1.6em;
      color: #333;
    }
    article .search li:hover p{
      text-decoration: underline;
    }
    article .search li p{
      margin: 50px 0 0 0;
    }
    article .search li:last-child p{
      margin: 0 0 50px 0;
    }
    article .book{
      margin: 50px 0;
      padding-top: 50px;
      background-color: bisque;
    }
    article .book h3{
      text-align: start;
      font-size: 2em;
      margin-bottom: 50px;
      padding-left: 4%;
    }
    article .book img{
      width: 80%;
      box-shadow: 1px 1px 4px 4px rgba(0,0,0,.3);
    }
    article .book dl{
      padding: 0 13%;
      margin-top: 15px;
    }
    article .book dl dt{
      font-weight: 700;
    }
    article .book dl dd{
      color: #666;
      text-align: justify;
    }
    article .book dl span{
      display: block;
      margin: 10px 0;
      color: #999;
    }
    .swiper-button-prev::after,.swiper-button-next:after{
      color:#6f480a
    }
    @media  screen and (max-width: 1200px){
      article .search{
        flex-direction: column-reverse;
      }
      article .search li{
        width: 100%;
        padding: 0 6%;
      }
    }
  </style>